import React, {Component} from 'react';
import {connect} from 'react-redux';

import {addApiKey} from '../actions';

class APIKeyForm extends Component {
	componentWillMount() {
		const searchParams = new URLSearchParams(window.location.search);
		let apikey = searchParams.get('apikey');
		if ('string' === typeof apikey && '' !== apikey.trim('')) {
			this.props.addApiKey(apikey);
		}
	}

	handleSubmit(event) {
		event.preventDefault();
		const apikey = document.getElementById('addAPIKey').value;
		this.props.addApiKey(apikey);
	}

	render() {
		return (<div className="col-sm-4 offset-sm-4 mb-3">
			<form className="input-group mb-3"
			      onSubmit={this.handleSubmit.bind(this)}>
				<input id="addAPIKey" className="form-control"
				       placeholder="API Key"
				       type="text"/>
				<div className="input-group-append">
					<button onClick={this.handleSubmit.bind(this)}
					        className="btn btn-secondary">Submit
					</button>
				</div>
			</form>
			<p>Get an <a
				href="https://www.alphavantage.co/support/#api-key">API
				key</a> to continue.<br/>
				<small><em>Note: This tool is powered by, but not affiliated
					with Alpha Vantage</em>.
				</small>
			</p>
		</div>);
	}
}

export default connect(null, {addApiKey})(APIKeyForm);


