import axios from 'axios';

export const ADD_APIKEY = 'ADD_APIKEY';
export const UPDATE_HIGHEST_TOTAL = 'UPDATE_HIGHEST_TOTAL';
export const UPDATE_POTENTIAL_HIGHEST_TOTAL = 'UPDATE_POTENTIAL_HIGHEST_TOTAL';
export const ADD_PORTFOLIO = 'ADD_PORTFOLIO';
export const REMOVE_PORTFOLIO = 'REMOVE_PORTFOLIO';
export const ADD_TO_PORTFOLIO = 'ADD_TO_PORTFOLIO';
export const ADD_SYMBOL = 'ADD_SYMBOL';
export const REMOVE_SYMBOL = 'REMOVE_SYMBOL';
export const UPDATE_SYMBOL_DATA = 'UPDATE_SYMBOL_DATA';
export const CHANGE_ALLOCATION = 'CHANGE_ALLOCATION';
export const CHANGE_OWNED = 'CHANGE_OWNED';
export const CHANGE_ACTIVE_PORTFOLIO = 'CHANGE_ACTIVE_PORTFOLIO';

const baseUrl = `https://www.alphavantage.co/query`;

export function addSymbol(symbol, portfolio) {
	return function(dispatch) {
		if ('' !== symbol.trim()) {
			dispatch({
				         type: ADD_SYMBOL,
				         payload: symbol,
			         });
			dispatch({
				         type: ADD_TO_PORTFOLIO,
				         payload: {symbol: symbol.toUpperCase(), portfolio},
			         });
		}
	};
}

export function removeSymbol(portfolio, symbol) {
	return function(dispatch) {
		if ('' !== symbol.trim()) {
			dispatch({
				         type: REMOVE_SYMBOL,
				         payload: {portfolio, symbol},
			         });
		}
	};
}

export function removePortfolio(portfolio) {
	return function(dispatch) {
		dispatch({
			         type: REMOVE_PORTFOLIO,
			         payload: portfolio,
		         });
	};
}

export function addApiKey(apiKey) {
	return function(dispatch) {
		dispatch({
			         type: ADD_APIKEY,
			         payload: apiKey,
		         });
	};
}

export function changeActivePortfolio(portfolioId) {
	return function(dispatch) {
		dispatch({
			         type: CHANGE_ACTIVE_PORTFOLIO,
			         payload: portfolioId,
		         });
	};
}

export function addPortfolio(newIndex) {
	return function(dispatch, store) {
		dispatch({
			         type: ADD_PORTFOLIO,
			         payload: newIndex,
		         });
	};
}

export function changeAllocation(portfolio, symbol, allocation) {
	return function(dispatch) {
		if ('' !== symbol.trim()) {
			dispatch({
				         type: CHANGE_ALLOCATION,
				         payload: {
					         portfolio, symbol, allocation,
				         },
			         });
		}
	};
}

export function changeSharesOwned(portfolio, symbol, owned) {
	return function(dispatch) {
		if ('' !== symbol.trim()) {
			dispatch({
				         type: CHANGE_OWNED,
				         payload: {
					         portfolio, symbol, owned,
				         },
			         });
		}
	};
}

export function getSymbolData(symbol) {
	return function(dispatch, getState) {
		const state = getState();
		axios.get(
			`${baseUrl}?apikey=${state.apikey}&function=TIME_SERIES_DAILY_ADJUSTED&outputsize=compact&symbol=${symbol}`)
			.then(response => {
				let data = 'undefined' !== typeof response.data['Time Series (Daily)'] ?
					response.data['Time Series (Daily)'] :
					{},
					adjustedClose = getAdjustedClose(response.data['Time Series (Daily)']);
				dispatch({
					         type: UPDATE_SYMBOL_DATA,
					         payload: {symbol, data, adjustedClose,},
				         });

			});
	};
}

export const getSymbolDataMiddleware = store => next => action => {
	if (action.type !== ADD_SYMBOL) {
		return next(action);
	}

	const state = store.getState(),
		symbol = action.payload.toUpperCase();
	axios.get(
		`${baseUrl}?apikey=${state.apikey}&function=TIME_SERIES_DAILY_ADJUSTED&outputsize=compact&symbol=${symbol}`)
		.then(response => {
			let data = 'undefined' !== typeof response.data['Time Series (Daily)'] ?
				response.data['Time Series (Daily)'] :
				{},
				adjustedClose = getAdjustedClose(response.data['Time Series (Daily)']);
			store.dispatch({
				               type: UPDATE_SYMBOL_DATA,
				               payload: {symbol, data, adjustedClose,},
			               });

		});

	next(action);
};

export const updateHighestTotal = store => next => action => {
	if (![UPDATE_SYMBOL_DATA, CHANGE_ALLOCATION, CHANGE_OWNED].includes(action.type)) {
		return next(action);
	}

	const state = store.getState(),
		symbol = action.payload.symbol.toUpperCase(),
		adjustedClose = ('undefined' !== typeof action.payload.adjustedClose) ?
			action.payload.adjustedClose :
			('undefined' !== typeof state.symbols[symbol] ? state.symbols[symbol].adjustedClose : 0);

	state.portfolios.forEach((portfolio, i) => {
		if ('undefined' !== typeof portfolio[symbol] && !isNaN(portfolio[symbol].allocation) &&
			0 < portfolio[symbol].allocation) {
			let portfolioHighestTotal = 0;
			Object.keys(portfolio).forEach(s => {
				const allocation = ('undefined' !== typeof action.payload.allocation && s === symbol) ?
					action.payload.allocation :
					portfolio[s].allocation,
					symbolAdjustedClose = (s === symbol) ? adjustedClose : state.symbols[s].adjustedClose,
					symbolTotal = (symbolAdjustedClose / allocation) * 100;
				if (Infinity !== symbolTotal && !isNaN(symbolTotal) && portfolioHighestTotal < symbolTotal) {
					portfolioHighestTotal = symbolTotal;
				}
			});

			if (!isNaN(portfolioHighestTotal)) {
				store.dispatch({
					               type: UPDATE_HIGHEST_TOTAL,
					               payload: {portfolio: i, total: portfolioHighestTotal},
				               });
			}

			let owned = ('undefined' !== typeof action.payload.owned) ? action.payload.owned : portfolio[symbol].owned,
				symbolPotentalTotal = ((owned * adjustedClose) / portfolio[symbol].allocation) * 100;
			if (('undefined' === typeof state.highestTotal[i]) || isNaN(state.highestTotal[i].potential) ||
				parseFloat(symbolPotentalTotal) > parseFloat(state.highestTotal[i].potential)) {
				store.dispatch({
					               type: UPDATE_POTENTIAL_HIGHEST_TOTAL,
					               payload: {portfolio: i, total: symbolPotentalTotal},
				               });
			}
		}
	});

	next(action);
};

export function getAmountToBuy(adjustedClose, allocation, highestTotal, shouldRound = false) {
	if (!isNaN(adjustedClose) && !isNaN(allocation) && allocation > 0) {
		let amount = highestTotal * (allocation / 100) / adjustedClose;
		return false === shouldRound ? amount : Math.round(amount);
	} else {
		return 0;
	}
}

export function buildUrl(portfolios, symbols) {
	return portfolios.map((portfolio) => {
		return Object.keys(portfolio).length ? '&portfolio[]=' + Object.keys(portfolio).map(symbol => {
			if ('' !== symbol && 'undefined' !== typeof symbols[symbol]) {
				return `${symbol}-${isNaN(portfolio[symbol].allocation) ?
					0 :
					portfolio[symbol].allocation}-${isNaN(portfolio[symbol].owned) ?
					0 :
					portfolio[symbol].owned}`;
			}
		}).join('~') : '';
	}).join('');
}

function getAdjustedClose(data) {
	const dataLabel = '5. adjusted close';
	if ('undefined' !== typeof data) {
		let latest = data[Object.keys(data)[0]];
		if ('undefined' !== typeof latest &&
			'undefined' !== typeof  latest[dataLabel]) {
			let adjustedClose = parseFloat(latest[dataLabel]);
			return adjustedClose.toFixed(2);
		}
	} else {
		return 'no data found';
	}
}




