import {ADD_SYMBOL, UPDATE_SYMBOL_DATA} from '../actions';

export default (state = {}, action) => {
	var newState = Object.assign({}, state);
	switch (action.type) {
		case ADD_SYMBOL:
			if ('undefined' === typeof newState[action.payload]) {
				newState[action.payload.toUpperCase()] = {};
			}
			break;
		case UPDATE_SYMBOL_DATA:
			newState[action.payload.symbol] = {
				data: action.payload.data,
				adjustedClose: action.payload.adjustedClose,
			};
			break;
		default:
			break;
	}
	return newState;
}




