import React, {Component} from 'react';
import {connect} from 'react-redux';

import {changeSharesOwned} from '../actions';

class PortfolioOwned extends Component {
	componentWillUpdate() {
		this.calculateTotal();
	}

	handleUpdateOwned(event) {
		this.props.changeSharesOwned(this.props.portfolioIndex, event.target.dataset.symbol, event.target.value);
	}

	getCurrentValue(symbol, owned) {
		if ('undefined' !== typeof this.props.symbols[symbol]) {
			let adjustedClose = parseFloat(this.props.symbols[symbol].adjustedClose);
			return !isNaN(adjustedClose) ? (adjustedClose * owned).toFixed(2) : 0;
		} else {
			return 0;
		}
	}

	calculateTotal() {
		let totalValue = 0;
		Object.keys(this.props.portfolio).forEach(symbol => {
			let currentValue = this.getCurrentValue(symbol, this.props.portfolio[symbol].owned);
			if (!isNaN(currentValue)) {
				totalValue += parseFloat(this.getCurrentValue(symbol, this.props.portfolio[symbol].owned));
			}
		});
		return totalValue;
	}

	getSharesToBuy(symbol) {
		const highestTotalPotential = 'undefined' !== typeof this.props.highestTotal[this.props.portfolioIndex]
			? this.props.highestTotal[this.props.portfolioIndex].potential : 0,
			sharesToBuy = (highestTotalPotential *
				((this.props.portfolio[symbol].allocation / 100) / this.props.symbols[symbol].adjustedClose)) -
				this.props.portfolio[symbol].owned;
		return sharesToBuy;
	}

	renderOwnedRows() {
		this.totalPercentDelta = 0;
		this.totalCost = 0;
		if (Object.keys(this.props.portfolio).length) {
			return Object.keys(this.props.portfolio).map(symbol => {
				if ('' !== symbol) {
					let currentPercent = ((this.getCurrentValue(symbol, this.props.portfolio[symbol].owned) /
						this.calculateTotal()) * 100),
						percentDelta = Math.abs(currentPercent - this.props.portfolio[symbol].allocation),
						sharesToBuy = !isNaN(Math.round(this.getSharesToBuy(symbol))) ?
							Math.round(this.getSharesToBuy(symbol)) :
							0,
						costOfShares = (sharesToBuy * this.props.symbols[symbol].adjustedClose);
					this.totalPercentDelta += percentDelta;
					this.totalCost += costOfShares;
					return (<tr key={symbol}>
						<td className="text-left align-middle">
							{symbol}
						</td>

						<td className="input-group input-group-sm align-middle">
							<input className="form-control percent"
							       placeholder="shares" onChange={this.handleUpdateOwned.bind(this)}
							       value={this.props.portfolio[symbol].owned}
							       data-symbol={symbol}
							       type="number" min="0"/>
						</td>


						<td className="align-middle">
							&#36;{this.getCurrentValue(symbol, this.props.portfolio[symbol].owned)}
						</td>

						<td className="align-middle">
							{currentPercent.toFixed(2)}&#37;
						</td>

						<td className="align-middle">
							&Delta; {percentDelta.toFixed(2)}&#37;
						</td>

						<td className="align-middle">
							{sharesToBuy}
						</td>

						<td className="align-middle">
							&#36;{costOfShares.toFixed(2)}
						</td>
					</tr>);
				}
			});
		} else {
			return <tr>
				<td colSpan="7">Add a symbol to begin</td>
			</tr>;
		}
	}

	renderOwnedSum() {
		if (Object.keys(this.props.portfolio).length) {
			return (<tr className="table-secondary">
				<td className="align-middle text-left" colSpan="2">
					<strong>Portfolio Total</strong>
				</td>

				<td className="align-middle">&#36;{this.calculateTotal().toFixed(2)}</td>

				<td>&nbsp;</td>

				<td className="align-middle">&Delta; {this.totalPercentDelta.toFixed(2)} &#37;</td>

				<td>&nbsp;</td>

				<td className="align-middle">&#36;{this.totalCost.toFixed(2)}</td>
			</tr>);
		}
	}

	render() {
		return (<div className="">
			<h3>Shares Owned</h3>
			<table className="table table-striped table-sm text-center">
				<thead>
					<tr>
						<th className="text-left">Symbol</th>
						<th>Shares</th>
						<th>Value</th>
						<th>Percent</th>
						<th>Percent &Delta;</th>
						<th>Shares to buy</th>
						<th>Cost</th>
					</tr>
				</thead>
				<tbody>
					{this.renderOwnedRows()}
				</tbody>
				<tfoot>
					{this.renderOwnedSum()}
				</tfoot>
			</table>
		</div>);

	}
}

function mapStateToProps({symbols, highestTotal}) {
	return {symbols, highestTotal};
}

export default connect(mapStateToProps, {changeSharesOwned})(PortfolioOwned);


