import React, {Component} from 'react';
import {connect} from 'react-redux';
import {getAmountToBuy, changeAllocation, getSymbolData} from '../actions';

import SymbolRow from './symbol-row';

class PortfolioBase extends Component {
	renderSymbolRows(portfolioIndex) {
		if (Object.keys(this.props.portfolio).length) {
			return Object.keys(this.props.portfolio)
				.map((symbol, i) => {
					if ('' !== symbol && 'undefined' !== typeof this.props.symbols[symbol]) {
						let adjustedClose = this.props.symbols[symbol].adjustedClose,
							allocation = this.props.portfolio[symbol].allocation,
							amountToBuy = ('undefined' !== typeof this.props.highestTotal[portfolioIndex]) ?
								getAmountToBuy(adjustedClose, allocation, this.props.highestTotal[portfolioIndex].minimum, true) :
								0;
						this.adjustedTotal += amountToBuy * adjustedClose;
						this.allocationTotal += parseFloat(allocation);
						return <SymbolRow symbol={symbol}
						                  allocation={allocation}
						                  portfolioIndex={portfolioIndex}
						                  adjustedClose={adjustedClose}
						                  key={i}/>;
					}
				});
		} else {
			return <tr>
				<td colSpan="5">Add a symbol to begin</td>
			</tr>;
		}
	}

	checkValidity() {
		return 100 < this.allocationTotal ? 'is-invalid' : '';
	}

	renderTotal() {
		return (
			<tr className="table-secondary">
				<td className="align-middle text-left">
					<strong>Base Portfolio Total</strong>
				</td>

				<td className="input-group input-group-sm align-middle">
					<input className={`form-control col-xs-3 percent ${this.checkValidity()}`}
					       placeholder="percent"
					       readOnly={true}
					       disabled={true}
					       value={isNaN(this.allocationTotal) ? 0 : this.allocationTotal.toFixed(2)}/>
					<div className="input-group-append">
						<div className="input-group-text">&#37;</div>
					</div>
				</td>

				<td className="align-middle">
					${isNaN(this.adjustedTotal) ? 0 : this.adjustedTotal.toFixed(2)}
				</td>
				<td>&nbsp;</td>
				<td>&nbsp;</td>
			</tr>
		);
	}

	equalizePortfolio(event) {
		const symbols = Object.keys(this.props.portfolio),
			percent = 100 / symbols.length;
		symbols.forEach(symbol => {
			this.props.changeAllocation(this.props.portfolioIndex, symbol, percent.toFixed(2));
		});
	}

	refreshAllPrices(event) {
		const symbols = Object.keys(this.props.portfolio);
		symbols.forEach(symbol => {
			this.props.getSymbolData(symbol);
		});
	}

	render() {
		this.adjustedTotal = 0;
		this.allocationTotal = 0;
		return <div className="table-responsive">
			<h3>Base Portfolio</h3>
			<table className="table table-striped table-sm text-center">
				<thead>
					<tr>
						<th className="text-left">Symbol</th>
						<th>Percent of total <button type="button" className="btn btn-outline-secondary btn-sm"
						                             onClick={this.equalizePortfolio.bind(this)}>Equalize
							Portfolio</button></th>
						<th>Current Price <button type="button" className="btn btn-outline-secondary btn-sm"
						                          onClick={this.refreshAllPrices.bind(this)}>Refresh Prices</button>
						</th>
						<th>Shares</th>
						<th>&nbsp;</th>
					</tr>
				</thead>
				<tbody>
					{this.renderSymbolRows(this.props.portfolioIndex)}
				</tbody>
				<tfoot>
					{this.renderTotal()}
				</tfoot>
			</table>
		</div>;
	}
}

function mapStateToProps({symbols, portfolios, highestTotal}) {
	return {symbols, highestTotal};
}

export default connect(mapStateToProps, {changeAllocation, getSymbolData})(PortfolioBase);


