import React, {Component} from 'react';
import {connect} from 'react-redux';

import {removeSymbol, changeAllocation, getAmountToBuy, getSymbolData} from '../actions';

class SymbolRow extends Component {
	updateAllocation(event) {
		this.props.changeAllocation(this.props.portfolioIndex, this.props.symbol, event.target.value);
	}

	removeSymbol(event) {
		this.props.removeSymbol(this.props.portfolioIndex, this.props.symbol);
	}

	handleRefresh(event) {
		this.props.getSymbolData(this.props.symbol);
	}

	render() {
		let highestTotalMin = 'undefined' !== typeof this.props.highestTotal[this.props.portfolioIndex] ?
			this.props.highestTotal[this.props.portfolioIndex].minimum :
			0;
		return (
			<tr>
				<td className="text-left align-middle">
					{this.props.symbol}
				</td>

				<td className="input-group input-group-sm align-middle">
					<input className={`form-control percent`}
					       placeholder="percent" onChange={this.updateAllocation.bind(this)}
					       value={this.props.allocation}
					       type="number" step="0.01"/>
					<div className="input-group-append">
						<div className="input-group-text">&#37;</div>
					</div>
				</td>

				<td className="align-middle">&#36;{this.props.adjustedClose}&nbsp;&nbsp;<i
					className="material-icons refresh" onClick={this.handleRefresh.bind(this)}>refresh</i></td>

				<td className="align-middle">{getAmountToBuy(this.props.adjustedClose, this.props.allocation, highestTotalMin, true)}</td>

				<td className="align-middle">
					<i className="material-icons"
					   onClick={this.removeSymbol.bind(this)}>delete</i>
				</td>
			</tr>
		);
	}
}

function mapStateToProps({highestTotal}) {
	return {highestTotal};
}

export default connect(mapStateToProps, {removeSymbol, changeAllocation, getSymbolData})(SymbolRow);



