import React, {Component} from 'react';
import {connect} from 'react-redux';

import {addSymbol} from '../actions';
import activePortfolio from '../reducers/activePortfolio-reducer';

class AddNew extends Component {
	addNewSymbol(event) {
		event.preventDefault();
		const newSymbol = document.getElementById('addNewSymbol').value;
		if (newSymbol.trim()) {
			this.props.addSymbol(newSymbol, this.props.activePortfolio);
		}
		document.getElementById('addNewSymbol').value = '';
	}

	render() {
		return (
			<form className="input-group  w-100" onSubmit={this.addNewSymbol.bind(this)}>
				<input id="addNewSymbol" className="form-control form-control-dark" placeholder="New Symbol"
				       type="text"/>
			</form>
		);
	}
}

function mapStateToProps({activePortfolio}) {
	return {activePortfolio};
}

export default connect(mapStateToProps, {addSymbol})(AddNew);



