import {combineReducers} from 'redux';

import symbols from './symbols-reducer';
import portfolios from './portfolio-reducer';
import apikey from './apikey-reducer';
import highestTotal from './highestTotal-reducer';
import activePortfolio from './activePortfolio-reducer';

export default combineReducers({symbols, portfolios, apikey, highestTotal, activePortfolio});



