import React, {Component} from 'react';
import {connect} from 'react-redux';

import AddNew from './add-new';

class Header extends Component {
	clearLastHistory() {

	}

	renderHeader() {
		if (this.props.apikey) {
			return (<nav className="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 mb-3">
				<span className="navbar-brand col-sm-3 col-md-2 mr-0">Diversify</span>
				<AddNew/>
				<ul className="navbar-nav px-3">
					<li className="nav-item text-nowrap">
						<a className="nav-link" href="/diversify">Clear</a>
					</li>
				</ul>
			</nav>);
		} else {
			return (<nav className="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 mb-3">
				<a className="navbar-brand col-sm-3 col-md-2 mr-0" href="#">Diversify</a>
			</nav>);
		}
	}

	render() {
		return this.renderHeader();
	}
}

function mapStateToProps({apikey}) {
	return {apikey};
}

export default connect(mapStateToProps)(Header);


