import {ADD_PORTFOLIO, UPDATE_HIGHEST_TOTAL, UPDATE_POTENTIAL_HIGHEST_TOTAL} from '../actions';

export default (state = [{minimum: 0, potential: 0}], action) => {
	var newState = [...state];
	switch (action.type) {
		case UPDATE_HIGHEST_TOTAL:
			if ('undefined' === typeof newState[action.payload.portfolio]) {
				newState[action.payload.portfolio] = {minimum: 0, potential: 0};
			}
			newState[action.payload.portfolio] = {
				minimum: action.payload.total,
				potential: newState[action.payload.portfolio].potential,
			};
			break;
		case UPDATE_POTENTIAL_HIGHEST_TOTAL:
			if ('undefined' === typeof newState[action.payload.portfolio]) {
				newState[action.payload.portfolio] = {minimum: 0, potential: 0};
			}
			newState[action.payload.portfolio] = {
				minimum: newState[action.payload.portfolio].minimum,
				potential: action.payload.total,
			};
			break;
		case ADD_PORTFOLIO:
			newState[action.payload] = {
				minimum: 0,
				potential: 0,
			};
			break;
		default:
			break;
	}
	return newState;
}



