import React, {Component} from 'react';
import {connect} from 'react-redux';

import Sidebar from './sidebar';
import PortfolioBase from './portfolio-base';
import PortfolioOwned from './portfolio-owned';

import {addSymbol, changeAllocation, changeSharesOwned, removePortfolio} from '../actions';

class Portfolios extends Component {
	componentWillMount() {
		const searchParams = new URLSearchParams(window.location.search);
		let pieces, portfolios = searchParams.getAll('portfolio[]');
		if (portfolios.length) {
			portfolios.forEach((p, ind) => {
				let portfolio = p.split('~');
				if (portfolio.length) {
					portfolio.forEach(symbol => {
						pieces = symbol.split('-');
						this.props.changeAllocation(ind, pieces[0], pieces[1]);
						this.props.changeSharesOwned(ind, pieces[0], pieces[2]);
						this.props.addSymbol(pieces[0], ind);
					});
				}
			});
		}
	}

	deletePortfolio() {
		this.props.removePortfolio(this.props.activePortfolio);
	}

	render() {
		return (<div className="row">
			<Sidebar/>
			<main role="main" className="col-md-9 ml-sm-auto col-lg-10 pt-3 px-4">
				<div
					className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mb-3 border-bottom">
					<h1 className="h2">Portfolio {this.props.activePortfolio}</h1>
					<div className="btn-toolbar mb-2 mb-md-0">
						<div className="btn-group mr-2">
							<button className="btn btn-sm btn-outline-danger"
							        onClick={this.deletePortfolio.bind(this)}>Delete
							</button>
						</div>
					</div>
				</div>
				<div className="tab-content mt-3">
					{this.props.portfolios.map((portfolio, i) => <div
						className={`tab-pane fade ${(0 === i) ? 'show active' : ''}`} key={i} id={`portfolio-${i}`}>
						<PortfolioBase portfolio={portfolio} portfolioIndex={i}/>
						<hr/>
						<PortfolioOwned portfolio={portfolio} portfolioIndex={i}/>
					</div>)}
				</div>
			</main>
		</div>);
	}
}

function mapStateToProps({symbols, apikey, portfolios, highestTotal, activePortfolio}) {
	return {symbols, apikey, portfolios, highestTotal, activePortfolio};
}

export default connect(mapStateToProps, {
	addSymbol,
	changeAllocation,
	changeSharesOwned,
	removePortfolio,
})(Portfolios);


