import './App.css';

import React, {Component} from 'react';
import {connect} from 'react-redux';

import Header from './components/header';
import Portfolios from './components/portfolios';
import APIKeyForm from './components/apikey-form';

import {buildUrl} from './actions';

class App extends Component {
	renderPortfolioOrApiKey() {
		if ('' === this.props.apikey) {
			return <APIKeyForm/>;
		} else {
			return <Portfolios/>;
		}
	}

	getLastPrice(symbol) {
		const dataLabel = '5. adjusted close';
		if ('undefined' !== typeof this.props.symbols[symbol] &&
			'undefined' !== typeof this.props.symbols[symbol][Object.keys(this.props.symbols[symbol])[0]] &&
			'undefined' !== typeof
				this.props.symbols[symbol][Object.keys(this.props.symbols[symbol])[0]][dataLabel]) {
			return this.props.symbols[symbol][Object.keys(this.props.symbols[symbol])[0]][dataLabel];
		}
		return 0;
	}

	componentDidUpdate() {
		let portfolioURI = buildUrl(this.props.portfolios, this.props.symbols);
		window.history.pushState({}, '', `/diversify/?apikey=${this.props.apikey}${portfolioURI}`);
	}

	render() {
		return (
			<div className="App">
				<Header/>
				<section className="container-fluid">
					{this.renderPortfolioOrApiKey()}
				</section>
			</div>
		);
	}
}

function mapStateToProps({symbols, apikey, portfolios, highestTotal}) {
	return {symbols, apikey, portfolios, highestTotal};
}

export default connect(mapStateToProps)(App);



