import {
	ADD_PORTFOLIO,
	ADD_TO_PORTFOLIO,
	REMOVE_PORTFOLIO,
	CHANGE_ALLOCATION,
	CHANGE_OWNED,
	REMOVE_SYMBOL,
} from '../actions';

export default (state = [{}], action) => {
	var newState = [...state],
		allocation, owned;
	switch (action.type) {
		case ADD_TO_PORTFOLIO:
			if ('undefined' === typeof newState[action.payload.portfolio]) {
				newState[action.payload.portfolio] = {};
			}
			if ('undefined' === typeof newState[action.payload.portfolio][action.payload.symbol]) {
				newState[action.payload.portfolio][action.payload.symbol] = {allocation: 0, owned: 0};
			}
			break;
		case ADD_PORTFOLIO:
			newState[action.payload] = {};
			break;
		case REMOVE_PORTFOLIO:
			newState[action.payload] = {};
			newState = [...newState];
			break;
		case CHANGE_ALLOCATION:
			if ('undefined' === typeof newState[action.payload.portfolio]) {
				newState[action.payload.portfolio] = {};
			}
			if ('undefined' === typeof newState[action.payload.portfolio][action.payload.symbol]) {
				newState[action.payload.portfolio][action.payload.symbol] = {
					allocation: action.payload.allocation,
					owned: 0,
				};
			}
			newState[action.payload.portfolio][action.payload.symbol] = {
				allocation: action.payload.allocation,
				owned: newState[action.payload.portfolio][action.payload.symbol].owned,
			};
			break;
		case CHANGE_OWNED:
			if ('undefined' === typeof newState[action.payload.portfolio]) {
				newState[action.payload.portfolio] = {};
			}
			owned = isNaN(action.payload.owned) ? 0 : parseInt(action.payload.owned);
			allocation = ('undefined' !== typeof newState[action.payload.portfolio][action.payload.symbol]) ?
				newState[action.payload.portfolio][action.payload.symbol].allocation :
				0;
			newState[action.payload.portfolio][action.payload.symbol] = {allocation, owned};
			break;
		case REMOVE_SYMBOL:
			delete newState[action.payload.portfolio][action.payload.symbol];
			break;
		default:
			break;
	}
	return newState;
}



