import React, {Component} from 'react';
import {connect} from 'react-redux';

import {changeActivePortfolio, addPortfolio} from '../actions';

class Sidebar extends Component {
	handleTabClick(e) {
		this.props.changeActivePortfolio(e.target.dataset.portfolio);
	}

	handleAddPortfolioClick(e) {
		const newIndex = this.props.portfolios.length;
		this.props.addPortfolio(newIndex);
	}

	renderTabs() {
		return (
			this.props.portfolios.map((portfolio, i) => {
				return (<li className="nav-item" key={i}>
					<a className={`nav-link ${(this.props.activePortfolio === i) ? 'active' : ''}`} data-toggle="tab"
					   data-portfolio={i}
					   href={`#portfolio-${i}`} role="tab" aria-controls="home" aria-selected="true"
					   onClick={this.handleTabClick.bind(this)}>
						<i className="material-icons">{`folder${(this.props.activePortfolio === i) ?
							'_open' :
							''}`}</i>&nbsp;
						Portfolio {i}
					</a>
				</li>);
			})
		);
	}

	render() {
		return (<nav className="col-md-2 d-none d-md-block bg-light sidebar">
			<div className="sidebar-sticky">
				<ul className="nav flex-column">
					<li className="nav-item">
						<span className="nav-link active">
							<span data-feather="home"></span>
							Dashboard <span className="sr-only">(current)</span>
						</span>
					</li>
				</ul>

				<h6 className="sidebar-heading d-flex justify-content-between px-3 mt-4 mb-1 text-muted">
					<span>Portfolios</span>
					<i className="d-flex text-muted material-icons"
					   onClick={this.handleAddPortfolioClick.bind(this)}>add_circle_outline</i>
				</h6>
				<ul className="nav flex-column mb-2">
					{this.renderTabs()}
				</ul>
			</div>
		</nav>);
	}
}

function mapStateToProps({portfolios, activePortfolio}) {
	return {portfolios, activePortfolio};
}

export default connect(mapStateToProps, {changeActivePortfolio, addPortfolio})(Sidebar);


